.container {
  text-align: center;
  width: 100%;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-around;
  overflow: hidden;
  background-color: #34495e;
  height: 10vh;
  position: sticky;
  top: 0px;
  color: #239ce2;
}
img{
  width: 4rem;
  height: 4rem;
}
p{
  font-size: 1.125rem;
}