.container {
  padding: 10px ;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  background-color: #ecf0f1;
}

.heading {
  font-weight: 500;
  font-size: 2.1rem;
}

.toolbar {
  width: 100%;
  display: flex;
  gap: 40px;
  justify-content: space-between;
  align-items: center;
}

.colors {
  display: flex;
  gap: 20px;
  padding: 0 30px;
}

.colors .color {
  height: 36px;
  width: 36px;
  border-radius: 50%;
  background-color: #239ce2;
}
.colors .active {
  border: 3px solid #FC427B;
}

.toolbar button {
  padding: 8px 16px;
  border-radius: 5px;
  background-color: #34495e;
  color: #2ecc71;
  outline: none;
  border: none;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: 1px;
  display: flex;
  gap: 5px;
  align-items: center;
  cursor: pointer;
}
.toolbar button:hover{
  background-color: #2c3e50;
  padding: 10px 18px;
}

.toolbar button svg {
  height: 20px;
  width: 20px;
  color: #2ecc71;
}

.main {
  display: flex;
  flex-direction: column;
  gap: 30px;
  width: 100%;
}
