.container {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

label {
  font-weight: 500;
  font-size: 1rem;
}

input {
  border: 1px solid #636e72;
  outline: none;
  padding: 10px 12px;
  font-size: 1rem;
  border-radius: 5px;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
input::placeholder{
  color: #273c75;
}
input:hover {
  border: 1px solid #2d3436;
}